.PoemFlow {
    width: 100%;
    height: 40px;
    line-height: 40px;
    margin: 80px auto;
    background-color: rgba(42, 44, 22, 0.7);
    position: relative;
    -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px);
    /* filter: blur(7px); */
}

.target{
    -webkit-animation: flowing 60s linear;
            animation: flowing 60s linear;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    font-size: 16px;
    position: absolute;
    top: 0;
    margin: 0;
  }
  
@-webkit-keyframes flowing {
100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
}
  
@keyframes flowing {
100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
}

.PoemFlow p {
